import React from "react";

import Timeline from '@mui/lab/Timeline';
import TimelineItem from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineDot from '@mui/lab/TimelineDot';
import TimelineOppositeContent, {
    timelineOppositeContentClasses,
} from '@mui/lab/TimelineOppositeContent';


function HistoryAssesment(props) {


    const history = props.data;

    const timeline = history.map(function (value, index) {
        console.log(typeof value.created_at)
        console.log(value)
        let localDate = new Date(value.created_at).toLocaleString()
        return (
            <TimelineItem key={value.nilai+"X"+index}>
                <TimelineOppositeContent color="textSecondary">
                    {localDate}
                </TimelineOppositeContent>
                <TimelineSeparator>
                    <TimelineDot />
                    <TimelineConnector />
                </TimelineSeparator>
                <TimelineContent>{value.deskripsi}<div className="text-base font-semibold hover:text-lg hover:font-bold font-sans">Nilai : {value.nilai}</div></TimelineContent>
            </TimelineItem>
        )
    })

    return (
        <div className="w-full rounded overflow-hidden shadow-lg">
            <div className="px-6 py-4 text-formal">
                <div className="font-bold text-xl mb-2">History Assesment</div>
                <Timeline
                    sx={{
                        [`& .${timelineOppositeContentClasses.root}`]: {
                            flex: 0.2,
                        },
                    }}
                >
                    {timeline}
                    {timeline.length == 0 && (<TimelineItem>
                        <TimelineOppositeContent color="textSecondary">
                             
                        </TimelineOppositeContent>
                        <TimelineSeparator>
                            <TimelineDot />
                            {/* <TimelineConnector /> */}
                        </TimelineSeparator>
                        <TimelineContent>Belum ada data history</TimelineContent>
                    </TimelineItem>)}
                </Timeline>
            </div>
        </div>
    );
}

export default HistoryAssesment;