import React, { useEffect, useState } from 'react'
import jalanKeKiriDiabetesMellitus from '../../assets/mp4/melitius/standByKiri.mp4'
import jalanKeKananDiabetesMellitus from '../../assets/mp4/melitius/standByKanan.mp4'
import tengahDiabetesMellitus from '../../assets/mp4/melitius/standByTengah.mp4'
import loadingVideo from '../../assets/mp4/loading.mp4'

// import alexander from './../../assets/img/avatar/alexander-hipp-iEEBWgY_6lA-unsplash.jpg';
// import nicolas from './../../assets/img/avatar/nicolas-horn-MTZTGvDsHFY-unsplash.jpg';
import { useNavigate, useParams } from 'react-router-dom'

import RotateLeftIcon from '@mui/icons-material/RotateLeft';
import { Drawer } from '@mui/material'
import CancelIcon from '@mui/icons-material/Cancel';

import VerticalTab from './Components/VerticalTab';
import DataPasien from './Components/DataPasien';
import CatatanMedis from './Components/CatatanMedis';
import HistoryAssesment from './Components/HistoryAssesment';


import axios from "axios";
import tokenOrganizer from './../../util/tokenOrganizer';
import { CustomDrawer } from './Components/CustomDrawer';
import DataRegister from './Components/DataRegister'
const baseURL = "https://sinalacare-api.neonauby.com";

const Kasus = () => {
  const navigate = useNavigate();

  const params = useParams();
  const studi_kasus = params.studiKasus;

  const [kasus, setKasus] = useState({});
  const [history, setHistory] = useState({});

  useEffect(() => {
    let token = tokenOrganizer.getToken();

    axios
      .get(`${baseURL}/api/v1/studi_kasus/` + studi_kasus, {
        headers: {
          Authorization: "Bearer " + token
        }
      })
      .then((response) => {
        setKasus(response.data.data)
        setBackgroundImg(response.data.data.imageUrlTengan)
      }).catch((err) => {
        // setOpen(true)
      });

    axios
      .get(`${baseURL}/api/v1/studi_kasus/` + studi_kasus + '/assesment_history', {
        headers: {
          Authorization: "Bearer " + token
        }
      })
      .then((response) => {
        setHistory(response.data.data)
      }).catch((err) => {
        // setOpen(true)
      });
  }, [])

  function handleClickNavigate(path) {
    navigate(path);
  }

  const [show, setShow] = useState(false);
  const [displayName, setDisplayName] = useState('data_pasien');
  const [anchor, setAnchor] = useState('left');

  const toggleShow = (anchor, displayName) => {
    setDisplayName(displayName)
    setAnchor(anchor)
    setShow(!show)
  };
  useEffect(() => {
    document.title = 'Studi Kasus';
  }, []);

  // controller
  // 0 tengah, -1 kiri, +1 kanan
  const [lokasi, setLokasi] = useState(0);
  const [backgroundImg, setBackgroundImg] = useState(loadingVideo);


  useEffect(() => {
  }, [backgroundImg]);

  useEffect(() => {
    let timestamp = Date.now();
    if (lokasi === -1) {
      let state = kasus?.imageUrlKiri;
      if (!state) {
        state = jalanKeKiriDiabetesMellitus;
      }
      setBackgroundImg(state + "?_=" + timestamp);
    } else if (lokasi === 1) {
      let state = kasus?.imageUrlKanan;
      if (!state) {
        state = jalanKeKananDiabetesMellitus;
      }
      setBackgroundImg(state + "?_=" + timestamp);
    } else {
      let state = kasus?.imageUrlTengan;
      if (!state) {
        state = loadingVideo;
      }
      setBackgroundImg(state + "?_=" + timestamp);
    }
    // eslint-disable-next-line
  }, [lokasi]);

  console.log(backgroundImg)

  return (
    <div className='main'>
      <div className='overlay'></div>
      <video src={backgroundImg} autoPlay loop muted preload='true' />
      <div className='content'>
        <section className="absolute top-0 left-0 w-full p-2 z-10" id="header">
          <button className="bg-white hover:bg-slate-400 text-gray-600 border-1 border-neutral-700 hover:border-neutral-500 font-bold py-2 px-4 rounded inline-flex items-center"
            type="button"
            onClick={() => handleClickNavigate('/studi-kasus')}>
            <span>Kembali</span>
          </button>
          <img
            alt='Avatar Mahasiswa'
            // src={alexander}
            src={kasus?.mahasiswa?.imageUrl}
            className="img-avatar-kasus absolute top-14 rounded-full left-16 sm:left-16 md:left-32 lg:left-52"
            id="avatar-mahasiswa"
          />
          <img
            alt='Avatar Dosen'
            // src={nicolas}
            src={kasus?.dosen?.imageUrl}
            className="img-avatar-kasus absolute top-14 rounded-full right-16 sm:right-16 md:right-32 lg:right-52"
            id="avatar-dosen"
          />
        </section>
        <section className="absolute bottom-0 left-0 w-full p-2 z-10" id="header">
          <button className='absolute bottom-14 left-10 sm:left-10 md:left-32 lg:left-48 rotate-180'
            onClick={() => {
              if (lokasi > -1) {
                setLokasi(lokasi - 1)
              }
            }}>
            <RotateLeftIcon fontSize='large' />
          </button>
          <button className='absolute bottom-14 right-10 sm:right-10 md:right-32 lg:right-48'
            onClick={() => {
              if (lokasi < 1) {
                setLokasi(lokasi + 1)
              }
            }}>
            <RotateLeftIcon fontSize='large' />
          </button>
        </section>
        <aside id="sidebarLeft" className="absolute left-0 top-[25vh] flex flex-col">
          <div className="grid justify-items-start">
            <div className="w-10 h-40 flex items-center justify-center bg-sky-800 shadow-lg rounded-t rounded-b">
              <button type="button" onClick={() => toggleShow('left', 'data_pasien')}>
                <span className="pointer-cursor [writing-mode:vertical-lr] text-white">Data Pasien</span>
              </button>
            </div>
            {/* <div className="w-10 h-40 flex items-center justify-center bg-sky-800 shadow-lg rounded-b">
              <button type="button" onClick={() => toggleShow('left', 'catatan_medis')}>
                <span className="pointer-cursor [writing-mode:vertical-lr] text-white">Catatan Medis</span>
              </button>
            </div> */}
          </div>
        </aside>
        <aside id="sidebarRight" className="absolute right-0 top-[25vh] flex flex-col rotate-180">
          <div className="grid justify-items-start">
            <div className="w-10 h-40 flex items-center justify-center bg-sky-800 shadow-lg rounded-t">
              <button type="button" onClick={() => toggleShow('right', 'history_assesment')}>
                <span className="pointer-cursor [writing-mode:vertical-lr] text-white">History Assesment</span>
              </button>
            </div>
            <div className="w-10 h-40 flex items-center justify-center bg-sky-800 shadow-lg rounded-b">
              <button type="button"
                onClick={() => handleClickNavigate('/assesment/' + studi_kasus)}>
                <span className="pointer-cursor [writing-mode:vertical-lr] text-white">Assesment</span>
              </button>
            </div>
          </div>
        </aside>
        <Drawer
          anchor={anchor}
          open={show}
          onClose={() => toggleShow(anchor)}
          PaperProps={{
            sx: { width: "500px" },
          }}
        // sx={{
        //   bgcolor: 'rgb(240,240,240)',
        // }}
        >
          <div className='w-full'>
            <div className="container mx-auto p-4">
              <div className="grid">
                <div className='flex justify-end'>
                  {/* <button type="button" onClick={() => toggleShow(anchor)}>
            <CancelIcon fontSize='large' htmlColor='#888888' />
            </button> */}
                </div>
              </div>
              <div className="grid pt-5">
                <div className='flex justify-center'>
                  {displayName === 'data_pasien' && <DataRegister encryptedId={studi_kasus} pasien={kasus} />}
                  {displayName === 'catatan_medis' && <CatatanMedis />}
                  {displayName === 'history_assesment' && <HistoryAssesment data={history} />}
                </div>
              </div>
            </div>
          </div>
        </Drawer>
      </div>
    </div>
  );
}

export default Kasus;